.list {
  display: block;
  flex-direction: column;
  list-style: none;
  justify-content: center;
}
.item {
  display: flex;
  flex-direction: row;
  justify-content: 0 auto;
}
.avatar {
  border-radius: 30%;
  margin: 10px;
}
.name {
  text-align: center;
  margin: 10px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 20px;
}
.statusIsOnline {
  display: block;
  background-color: green;

  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 20px;
}
.statusIsOffline {
  display: block;
  background-color: red;

  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 20px;
}
